@import url("https://fonts.googleapis.com/css?family=Satisfy&display=swap");

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  font-size: 62.5%;
  height: 100vh;
}

body {
  height: 100vh;
  margin: 0;
  font-family: "Satisfy", cursive;
  font-weight: 400;
  line-height: 1.7;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-image: -webkit-gradient(linear, left top, right top, from(#fa709a), to(#fee140));
  background-image: -o-linear-gradient(left, #fa709a 0%, #fee140 100%);
  background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%);
  overflow: auto;
}

.wrapper {
  width: 100vw;
  height: 100vh;
}

.hero {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[3];
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  padding-top: 6.8rem;
}

.hero h1 {
  font-size: 4.8rem;
}

.search-form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 2rem;
}

.search-box {
  text-align: center;
  width: 20rem;
  height: 4rem;
  background: #fff;
  border: none;
}

.search-box::-webkit-input-placeholder {
  font-family: "Satisfy", cursive;
  font-size: 2rem;
}

.search-box::-moz-placeholder {
  font-family: "Satisfy", cursive;
  font-size: 2rem;
}

.search-box:-ms-input-placeholder {
  font-family: "Satisfy", cursive;
  font-size: 2rem;
}

.search-box::-ms-input-placeholder {
  font-family: "Satisfy", cursive;
  font-size: 2rem;
}

.search-box::placeholder {
  font-family: "Satisfy", cursive;
  font-size: 2rem;
}

.search-form .btn {
  width: 22.8rem;
  height: rem;
  margin-top: 2rem;
  background: #f9d248;
  -webkit-box-shadow: 0 0.7rem 1rem rgba(0, 0, 0, 0.5);
          box-shadow: 0 0.7rem 1rem rgba(0, 0, 0, 0.5);
  border-radius: 1rem;
  cursor: pointer;
  border: none;
  font-family: "Satisfy", cursive;
  font-size: 3.6rem;
}

.load-icon {
  text-align: center;

}

.display-detail {
  text-align: center;
  width: 40rem;
  height: 50rem;
  margin: 0 auto;
  background: #352121;
  -webkit-box-shadow: 1rem 1rem 1rem rgba(71, 9, 28, 0.5);
          box-shadow: 1rem 1rem 1rem rgba(71, 9, 28, 0.5);
  border-radius: 2.5rem; 
  margin-top: 2rem;

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.display-detail h4 {
  color: #fff;
  font-size: 3.6rem;
}

.display-detail ul {
  color: #fff;
  list-style: none;
  font-size: 2.4rem;
  margin-top: 2rem;
}

.temp-icon {
  font-size: 4rem;
}

.temp-icon h3 {
  color: #fff;
}

.footer-text {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-size: 3.6rem;
}

