@import url(https://fonts.googleapis.com/css?family=Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  height: 100vh;
}

body {
  height: 100vh;
  margin: 0;
  font-family: "Satisfy", cursive;
  font-weight: 400;
  line-height: 1.7;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-image: -webkit-gradient(linear, left top, right top, from(#fa709a), to(#fee140));
  background-image: -webkit-linear-gradient(left, #fa709a 0%, #fee140 100%);
  background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%);
  overflow: auto;
}

.wrapper {
  width: 100vw;
  height: 100vh;
}

.hero {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[3];
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  padding-top: 6.8rem;
}

.hero h1 {
  font-size: 4.8rem;
}

.search-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}

.search-box {
  text-align: center;
  width: 20rem;
  height: 4rem;
  background: #fff;
  border: none;
}

.search-box::-webkit-input-placeholder {
  font-family: "Satisfy", cursive;
  font-size: 2rem;
}

.search-box::-moz-placeholder {
  font-family: "Satisfy", cursive;
  font-size: 2rem;
}

.search-box:-ms-input-placeholder {
  font-family: "Satisfy", cursive;
  font-size: 2rem;
}

.search-box::-ms-input-placeholder {
  font-family: "Satisfy", cursive;
  font-size: 2rem;
}

.search-box::placeholder {
  font-family: "Satisfy", cursive;
  font-size: 2rem;
}

.search-form .btn {
  width: 22.8rem;
  height: rem;
  margin-top: 2rem;
  background: #f9d248;
  box-shadow: 0 0.7rem 1rem rgba(0, 0, 0, 0.5);
  border-radius: 1rem;
  cursor: pointer;
  border: none;
  font-family: "Satisfy", cursive;
  font-size: 3.6rem;
}

.load-icon {
  text-align: center;

}

.display-detail {
  text-align: center;
  width: 40rem;
  height: 50rem;
  margin: 0 auto;
  background: #352121;
  box-shadow: 1rem 1rem 1rem rgba(71, 9, 28, 0.5);
  border-radius: 2.5rem; 
  margin-top: 2rem;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.display-detail h4 {
  color: #fff;
  font-size: 3.6rem;
}

.display-detail ul {
  color: #fff;
  list-style: none;
  font-size: 2.4rem;
  margin-top: 2rem;
}

.temp-icon {
  font-size: 4rem;
}

.temp-icon h3 {
  color: #fff;
}

.footer-text {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-size: 3.6rem;
}

/* 62.5, 75, 56.25, 50 */
@media (max-width: 1639px) , (max-height: 850px) {
  html {
    font-size: 56.25%
  }
}

@media (max-width: 1370px) , (max-height: 780px) {
  html {
    font-size: 50%
  }
}

@media (max-width: 1200px) , (max-height: 695px) {
  html {
    font-size: 42.25%
  }
}

@media (max-width: 975px) , (max-height: 580px) {
  html {
    font-size: 37.5%
  }
}



@media (max-width: 823px) and (max-height: 525px) {
  html {
    font-size: 35%;
  }
  .display-detail {
    width: 100rem;
    height: 25rem;

  
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}

@media (max-width: 846px) and (max-height: 414px) {
  html {
    font-size: 35%;
  }
  .display-detail {
    width: 100rem;
    height: 25rem;

  
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}

@media (max-width: 568px) and (max-height: 320px) {
  .display-detail {
    width: 90rem;
    height: 18rem;
  }  

  .temp-icon {
    display: flex;
    align-items: center;
    justify-content: space-evenly
  }
}

@media (max-width: 360px) and (max-height: 640px) {
  html {
    font-size: 29%;
  }
}

@media (max-width: 320px) and (max-height: 568px) {
  html {
    font-size: 29%;
  }
}

@media (max-width: 500px) {
  .hero {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media (min-width: 1024px) and (min-height: 1366px) {
  html {
    font-size: 62.5%;
  }
}
